const _hoisted_2 = /*#__PURE__*/_createStaticVNode("<h2 class=\"text-lg text-center italic leading-none filter-outline\" data-v-2fea79ba>Introduction</h2><h3 class=\"text-sm filter-outline-xs mt-10 mb-3\" data-v-2fea79ba>1. Introducing Ourselves</h3><p data-v-2fea79ba> What are your coding skills? Have you ever build or designed a webpage?<br data-v-2fea79ba> What is your favourite or your least favourite website? What do you wish from this course? </p><h3 class=\"text-sm filter-outline-xs mt-10 mb-3\" data-v-2fea79ba>2. Introducing the Course</h3><p data-v-2fea79ba>This course aims at providing basic understanding of web development. For this purpose, on the technical side, we will start with a very basic introduction to HTML and CSS. We will not have a fully extensive course on everything there is to know. Instead, we want learn together how to extend our knowledge from there and share useful resources.</p><p data-v-2fea79ba>Content-wise, we will investigate how the current web and its conventions are shaped by template culture. We will approximate this topic in small code experiments, investigating reoccuring website elements. In the end, these small experiments will compile into a collective, alternative website template. Thereby, we want to attain an understanding for contemporary discussion revolving around the web, for different tools and for the context in which websites are published.</p><p data-v-2fea79ba>Learning from each other and sharing knowledge is an important part of the course. Every code snippet and observeration is therefore considered open-source and free for everyone else to use. We also try to make everything happen within the predefined course times – no homeworks.</p><h3 class=\"text-sm filter-outline-xs mt-10 mb-3\" data-v-2fea79ba>3. Code Editor Setup </h3><ol class=\"list-decimal px-10 py-3 border border-secondary rounded-md mx-3\" data-v-2fea79ba><li data-v-2fea79ba>Start <a target=\"_blank\" href=\"https://code.visualstudio.com/\" data-v-2fea79ba>VS Code</a></li><li data-v-2fea79ba>Install <a target=\"_blank\" href=\"https://marketplace.visualstudio.com/items?itemName=ritwickdey.LiveServer\" data-v-2fea79ba>VS Code Live Server</a></li><li data-v-2fea79ba>Download the <a target=\"_blank\" download href=\"https://marketplace.visualstudio.com/items?itemName=ritwickdey.LiveServer\" data-v-2fea79ba>HTML boilderplate setup</a></li><li data-v-2fea79ba>Duplicate the folder and give it a new name</li><li data-v-2fea79ba>Open the folder in VS code</li><li data-v-2fea79ba>Start a live server</li></ol><h3 class=\"text-sm filter-outline-xs mt-10 mb-3\" data-v-2fea79ba>4. A Short Introduction to HTML </h3><ol class=\"mt-3 text-xs\" data-v-2fea79ba><h4 class=\"text-sm filter-outline-xs\" data-v-2fea79ba>Tutorials</h4><li data-v-2fea79ba><a href=\"https://developer.mozilla.org/en-US/docs/Learn/HTML/Introduction_to_HTML\" target=\"_blank\" data-v-2fea79ba>HTML Basics</a></li><li data-v-2fea79ba><a href=\"https://htmlreference.io/\" target=\"_blank\" data-v-2fea79ba>A List of HTML Tags</a></li></ol><div class=\"border border-secondary p-3 my-3 mx-6 rounded-md\" data-v-2fea79ba><ol class=\"list-decimal px-6\" data-v-2fea79ba><li data-v-2fea79ba>Duplicate the boilerplate and give it a new name</li><li data-v-2fea79ba>Find 5 different tags <a href=\"https://htmlreference.io/\" target=\"_blank\" data-v-2fea79ba>here</a> and paste them in your own HTML document</li><li data-v-2fea79ba>Add some example content</li><li data-v-2fea79ba>Check how their default style looks in different browsers</li><li data-v-2fea79ba>Explain them to your neighbour</li></ol></div><div class=\"border border-secondary p-3 my-3 mx-6 rounded-md\" data-v-2fea79ba><ol class=\"list-decimal px-6\" data-v-2fea79ba><li data-v-2fea79ba>Duplicate the boilerplate and give it a new name</li><li data-v-2fea79ba>Chose a song, a short text or a poem which you like</li><li data-v-2fea79ba>Find the text snippet and paste it into the index file</li><li data-v-2fea79ba>Illustrate the text using only HTML elements. For example, use <a target=\"_blank\" href=\"https://htmlreference.io/element/table/\" data-v-2fea79ba>tables</a>, <a target=\"_blank\" href=\"https://htmlreference.io/element/h1/\" data-v-2fea79ba>headlines</a>, <a target=\"_blank\" href=\"https://htmlreference.io/element/input/\" data-v-2fea79ba>input fields</a>, <a target=\"_blank\" href=\"https://htmlreference.io/element/li/\" data-v-2fea79ba>lists</a>, <a target=\"_blank\" href=\"https://htmlreference.io/element/summary/\" data-v-2fea79ba>summarys</a>.</li><li data-v-2fea79ba>Open your website in different browsers and check if it looks different</li><li data-v-2fea79ba>Present your website to the group</li></ol></div><ol class=\"mt-3\" data-v-2fea79ba><h4 class=\"text-sm filter-outline-xs\" data-v-2fea79ba>Collection</h4><li data-v-2fea79ba><a href=\"http://sebastianlyserena.dk/\" target=\"_blank\" data-v-2fea79ba>http://sebastianlyserena.dk/</a></li><li data-v-2fea79ba><a href=\"https://pitscher.net/index_v1.7.txt\" target=\"_blank\" data-v-2fea79ba>https://pitscher.net/index_v1.7.txt</a></li></ol><h3 class=\"text-sm filter-outline-xs mt-10 mb-3\" data-v-2fea79ba>5. A Short Introduction to CSS </h3><ol class=\"mt-3 text-xs\" data-v-2fea79ba><h4 class=\"text-sm filter-outline-xs\" data-v-2fea79ba>Tutorials</h4><li data-v-2fea79ba><a href=\"https://developer.mozilla.org/en-US/docs/Learn/Getting_started_with_the_web/CSS_basics\" target=\"_blank\" data-v-2fea79ba>CSS Basics</a></li><li data-v-2fea79ba><a href=\"https://learnlayout.com/\" target=\"_blank\" data-v-2fea79ba>CSS Layout</a></li><li data-v-2fea79ba><a href=\"https://cssreference.io/\" target=\"_blank\" data-v-2fea79ba>A List of CSS Attributes</a></li></ol><div class=\"border border-secondary p-3 my-3 mx-6 rounded-md\" data-v-2fea79ba><ol class=\"list-decimal px-6\" data-v-2fea79ba><li data-v-2fea79ba>Go to your favorite website</li><li data-v-2fea79ba>Open the inspector</li><li data-v-2fea79ba>Change the look of as many elements as possible by changing the <a target=\"_blank\" href=\"https://cssreference.io/property/background-color/\" data-v-2fea79ba>background color</a>, the <a target=\"_blank\" href=\"https://cssreference.io/property/border/\" data-v-2fea79ba>border</a> and the <a target=\"_blank\" href=\"https://cssreference.io/property/border-radius/\" data-v-2fea79ba>border radius</a>. <i data-v-2fea79ba>Hint: Don&#39;t reload the browser or your changes will get lost.</i></li><li data-v-2fea79ba>Advanced: Add <a target=\"_blank\" href=\"https://cssreference.io/property/box-shadow/\" data-v-2fea79ba>box shadows</a>. You can generate cool box shadows with <a target=\"_blank\" href=\"https://html-css-js.com/css/generator/box-shadow/\" data-v-2fea79ba>this generator</a>.</li><li data-v-2fea79ba>Document the results with screenshots</li><li data-v-2fea79ba>Add the screenshots to our <a href=\"https://www.are.na/karen-czock/dt-output\" data-v-2fea79ba>are.na output channel</a></li></ol></div><div class=\"border border-secondary p-3 my-3 mx-6 rounded-md\" data-v-2fea79ba><ol class=\"list-decimal px-6\" data-v-2fea79ba><li data-v-2fea79ba>Look around</li><li data-v-2fea79ba>Pick a view, an object or an arrangement of objects which you find interesting</li><li data-v-2fea79ba>Duplicate the boilerplate and give it a new name</li><li data-v-2fea79ba>Try to recreate it using only HTML and CSS. Check out different ways to position your elements <a target=\"_blanl\" href=\"https://www.are.na/block/21328405\" data-v-2fea79ba>here</a></li><li data-v-2fea79ba>Advanced: Make your website interactive using <a target=\"_blank\" href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/:hover?retiredLocale=de\" data-v-2fea79ba>hover</a></li><li data-v-2fea79ba>Advanced: Make your website responsive using <a target=\"_blank\" href=\"https://www.w3schools.com/css/css_rwd_mediaqueries.asp\" data-v-2fea79ba>media queries</a></li><li data-v-2fea79ba>Present your website to the group</li></ol></div><ol class=\"mt-3 text-xs\" data-v-2fea79ba><h4 class=\"text-sm filter-outline-xs\" data-v-2fea79ba>Collection</h4><li data-v-2fea79ba><a href=\"https://www.thisgrey.com/\" target=\"_blank\" data-v-2fea79ba>https://www.thisgrey.com/</a></li><li data-v-2fea79ba><a href=\"https://www.openthiswindow.com/\" target=\"_blank\" data-v-2fea79ba>https://www.openthiswindow.com/</a></li><li data-v-2fea79ba><a href=\"https://www.eggalone.com/\" target=\"_blank\" data-v-2fea79ba>https://www.eggalone.com/</a></li><li data-v-2fea79ba><a href=\"https://root.schloss-post.com/\" target=\"_blank\" data-v-2fea79ba>https://root.schloss-post.com/</a></li></ol><h3 class=\"text-sm filter-outline-xs mt-10 mb-3\" data-v-2fea79ba>7. Further Reading</h3><a class=\"block\" target=\"_blank\" href=\"https://html.energy/podcast.html\" data-v-2fea79ba>HTML Enegery Podcast (Laurel Schwulst)</a><a class=\"block\" target=\"_blank\" href=\"https://www.bloomberg.com/graphics/2015-paul-ford-what-is-code/\" data-v-2fea79ba>What is Code (Paul Ford)</a><a class=\"block\" target=\"_blank\" href=\"https://www.youtube.com/watch?v=aHUtMbJw8iA\" data-v-2fea79ba>Why is CSS so weird? (Miriam Suzanne)</a><a class=\"block\" target=\"_blank\" href=\"https://brutalistwebsites.com/\" data-v-2fea79ba>Brutalist Websites</a>", 24);
